@font-face {
  font-family: RobotoCondensed;
  src: url(assets/roboto-condensed.light.ttf);
  font-weight: 800;
}

@font-face {
  font-family: EconomicaBold;
  src: url(assets/economica-bold.ttf);
}

* {
  box-sizing: border-box;
}

body {
  /* text-align: center; */
  font-size: 20px;
  font-family: 'RobotoCondensed';
  background-color: black;
  color: white;
  font-size: 12px;
}

h1, h2, h3, h4, h5 {
  font-family: 'EconomicaBold';
}

/* ----- INTRO SECTION ------ */

.intro {
  height: calc(100vh - 100px);
}

.intro__header {
  font-size: 9em;
  text-align: left;
}

.intro__section {
  position: relative;
  top: 20%;
  left: 20%;
  width: 50%;
}

.intro__name {
  font-size: 4em;
}

.arrow {
  position: relative;
  left: 50%;
  top: 50%;
  /* left: 50%;
  bottom: -50%; */
  font-size: 3em;
}

.divider {
  width: 100%;
}

hr {
  display: block;
  border-width: 5px;
  color: white;
  background-color: white;
}

.connection__logos {
  font-size: 3em;
  margin-right: 2em;
  transition: all .2s ease-in-out;
}

.connection__logos:hover {
  transform: scale(1.5);
}

.title__fade__out {
  opacity: 0;
  animation-name: fade__out;
  animation-duration: 1s;
}

.title__fade__in {
  opacity: 100;
  animation-name: fade__in;
  animation-duration: 1s;
}

@keyframes fade__out {
  from {opacity: 100}
  to {opacity: 0}
}

@keyframes fade__in {
  from {opacity: 0}
  to {opacity: 100}
}

a, a:hover {
  color: white;
}


/* ----- ABOUT ------ */

.about {
  /* background: gray; */
  height: 100vh;
}

.about__container {
  width: 65%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 100px;
}

.about__photo {
  width: 50%;
}

.about__summary {
  width: 50%;
}

.about__photo {
  /* margin-top: 70px; */
  height: 400px;
  width: 400px;
  position: relative;
  top: 80px;
  margin-left: 50px;
}

.about__summary {
  padding: 5%;
  
}

.about__summary__greetings {
  font-size: 2em;
}

.about__summary__description {
  font-size: 1.5em;
}

.about__photo__image {
  width: 100%;
  height: 100%; 
}

.about__photo img {
  border-radius: 50%;
}

/* ----- HEADER ------ */
.nav {
  height: 100px;
}


