.each-project {
  width: 300px;
  border: 2px solid white;
  padding: 1em 1em 1em 1em;
  border-radius: 1em;
}

.each-project:not(h3) {
  font-size: 1.4em;
}

.each-project--image-container {
  width: 100%;
  height: 180px;
}

.each-project--image-container img {
  max-width: 100%;
  height: 100%;
  border-radius: 3%;
}

.each-project--links {
  display: flex;
  margin-top: .5em;
}

.each-project--links * {
  width: 50%;
  text-align: center;
}